.currency-selected, .currency-unselected {
	font-size: 16px;
	padding: 8px;
	margin: 8px;
	border: 1px solid #eee;
  -webkit-box-shadow: 0px 2px 3px 1px rgba(255, 255, 255, 0.2);
          box-shadow: 0px 2px 3px 1px rgba(255, 255, 255, 0.2);
  -webkit-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.currency-selected:hover, .currency-unselected:hover {
	-webkit-box-shadow: 0px 3px 4px 2px rgba(255, 255, 255, 0.3);
	        box-shadow: 0px 3px 4px 2px rgba(255, 255, 255, 0.3);
}

.currency-unselected {
	background-color: transparent;
	color: #eee;
}

.currency-unselected:hover {
	background-color: #333;
}

.currency-selected {
	background-color: #eee;
	color: #222;
}

.currency-selected:hover {
	background-color: #ddd;
}



